    <el-calendar v-model="value"></el-calendar>
    <el-button type="danger" icon="el-icon-back" circle @click="back"></el-button>
  </div>
</template>

<script>
export default {
    name:'ContentC',
    data() {
        return {
           value: new Date()
        }
    },
    methods:{
      back(){
        this.$store.commit("Backstore");
      }
    }

}
</script>

